.body {
    background-color: #8f8f93;
}

.portfolioContainer {
    width: 100%;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0px;
    left: 0;
    right: 0;
    margin-top: 50px;
    overflow-y: scroll;
    transform: scale(0.6);
    border-radius: 0px;
    border: 2px solid black;
}

.portfolioContainerFull {
    width: 100%;
    height: 100%;
    margin: auto;
    /* position: absolute; */
    top: 0;
    bottom: 0px;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background-color: white;
}

.portfolioBodyCont {
    position: absolute;
    background-color: white;
    width: 100%;
}

.portfolioPage1 {
    width: 100%;
    height: 100%;
    background-color: none;
    position: absolute;
    color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.portfolioPage1Details {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.portfolioPage1SocialHolder {
    margin-top: 100px;
}

.portfolioUserImage {
    width: 200px;
    height: 200px;
    border-radius: 100%;
    margin-right: 40px;
}

.portfolioUserDetails {
    font-size: 35px;
    margin-left: 40px;
}

.portfolioUserName {
    font-weight: 900;
    letter-spacing: 1px;
}

.portfolioUserLabel {
    font-weight: 600;
    letter-spacing: 2px;
}

.miniLine {
    width: 20%;
    height: 2px;
    background-color: #b22222;
    margin: auto;
    margin-top: 15px;
    margin-bottom: 15px;
}

/* ------------------------------------------------------- */
.portfolioBtn {
    font-family: 'Cereal';
    font-weight: 600;
    width: 200px;
    text-align: center;
    cursor: pointer;
    font-weight: 600;
    height: 45px;
    border-radius: 50px;
    background: linear-gradient(145deg, #99999d, #818184);
    box-shadow: 23px 23px 58px #7c7c80, -23px -23px 58px #a2a2a6;
    transition: 0.2s ease-out;
    color: #000000;
    line-height: 45px;
    border-radius: 15px;
}

.portfolioBtn:hover {
    font-weight: 600;
    width: 200px;
    text-align: center;
    cursor: pointer;
    font-weight: 400;
    height: 45px;
    background: #8f8f93;
    box-shadow: 23px 23px 58px #7c7c80, -23px -23px 58px #a2a2a6;
    transition: 0.2s ease-out;
    color: #000000;
    line-height: 45px;
    border-radius: 15px;
}

.portfolioBtn:active {
    font-weight: 600;
    width: 200px;
    text-align: center;
    cursor: pointer;
    font-weight: 400;
    height: 45px;
    background: linear-gradient(145deg, #818184, #99999d);
    box-shadow: 23px 23px 58px #7c7c80, -23px -23px 58px #a2a2a6;
    transition: 0.2s ease-out;
    color: #000000;
    line-height: 45px;
    border-radius: 15px;
}

.portfolioPage {
    width: 100%;
    height: auto;
    padding-bottom: 40px;
}

/* ----------------------------------------------------------- */
.aboutPage {
    background-color: #081b33;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-bottom: 0px;
}

.portfolioPageSplit {
    width: 50%;
    min-height: 400px;
    height: auto;
}

.leftAbout {
    width: 50%;
    background-color: #aeaeb2;
    color: #000000;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: center;
    font-size: 2em;
    font-weight: 700;
    margin-left: auto;
}

.aboutCard {
    opacity: 0.8;
    background-color: #081b33;
    padding: 50px;
    border-radius: 20px;
    background: linear-gradient(45deg, #bababe, #9d9da0);
    box-shadow: 23px -23px 58px #97979b, -23px 23px 58px #c5c5c9;
}

.rightAbout {
    color: #000000;
    background-color: #c7c7cc;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.portfolioUserSummary {
    width: 60%;
    border-radius: 20px;
    padding: 20px;
    border-radius: 20px;
    background: linear-gradient(45deg, #d5d5da, #b3b3b8);
    box-shadow: 14px -14px 52px #adadb1, -14px 14px 52px #e1e1e7;
    z-index: 1;
}

/* ---------------------------------------------------------- */
.workPage {
    background-color: #e6e6e6;
}

.portfolioPageTitle {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: #000000;
    text-align: center;
    padding-top: 40px;
    font-weight: 600;
    font-size: 26px;
    background-color: #d4d1d6;
