}

/* .portfolioCardTitle{
  background-color: red;
} */
.portfolioCardTitleMain {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 500;
}

.portfolioCardTitleSub {
    text-transform: capitalize;
    font-weight: 300;
    font-size: 18px;
}

.miniCardLine {
    margin-right: auto;
    margin-left: 0px;
}

.portfolioCardContent {
    width: 100%;
    text-align: justify;
}

.portfolioCardWebsite {
    margin-top: 20px;
    font-size: 15px;
}

.portfolioCardWebsite span {
    font-weight: 600;
    text-transform: uppercase;
}

.portfolioCardWebsite a {
    color: #b22222;
    text-decoration: none;
}

.portfolioCard:hover .portfolioCardWebsite a {
    color: #000000;
    text-decoration: none;
}

.portfolioCardDateContainer {
    width: 100%;
    height: 50px;
    line-height: 50px;
