.portfolioWorkCard {
    background-color: #15253f;
    color: #000000;
    width: 300px;
    min-height: 380px;
    height: auto;
    padding: 40px;
    border-radius: 20px;
    border-radius: 20px;
    background: #e5e5ea;
    background: linear-gradient(45deg, #d4d4da, #b2b2b8);
    box-shadow: 14px -14px 52px #acacb1, -14px 14px 52px #e0e0e7;
    transition: 0.2s ease-out;
}

.portfolioCardContainer {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 30px;
    padding-bottom: 30px;
    background: #c6c6cc;
    transition: 0.2s ease-out;
}

.portfolioWorkCard:hover {
    background: #697280;
    opacity: 1;
    color: #def2f1;
