}

/* ------------------------------------------------------------ */
.portfolioFlatContainer {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 30px;
    padding-bottom: 30px;
    background: #c6c6cc;
}

.portfolioFlat {
    background-color: #15253f;
    color: #000000;
    width: 300px;
    min-height: 380px;
    height: auto;
    padding: 40px;
    border-radius: 20px;
    border-radius: 20px;
    background: #e5e5ea;
    background: linear-gradient(45deg, #d4d4da, #b2b2b8);
    box-shadow: 14px -14px 52px #acacb1, -14px 14px 52px #e0e0e7;
    transition: 0.2s ease-out;
}

.portfolioFlat:hover {
    background: #697280;
    opacity: 1;
    color: #def2f1;
}

.portfolioFlat:hover .portfolioFlatSubTitle {
    color: black;
}

.portfolioFlat:hover .portfolioCardDateContainer {
    background-color: #4a4a53;
    border: none;
    color: #e5e5ea;
}

.portfolioFlatSubTitle {
    transition: 0.2s ease-out;
    color: #152642;
    text-transform: capitalize;
    font-weight: 400;
    margin-top: 10px;
}

.portfolioFlatContentSummary {
    margin-top: 10px;
}

/* ---------------------------------------------------- */
.portfolioExtrasContainer {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: #c6c6cc;
}

.extrasPage {
    background-color: #c6c6cc;
    padding-bottom: 30px;
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.floodFill {
    display: flex;
    width: 200px;
    height: 30px;
    border-radius: 50px;
    background: #c6c6cc;
    box-shadow: 14px -14px 52px #b4b4ba, -14px 14px 52px #d8d8de;
    padding-top: 15px;
    padding-bottom: 15px;
}

.portfolioExtraCard {
    font-family: 'Cereal' !important;
    color: #b22222 !important;
    display: flex;
    margin-top: 50px;
    width: 500px;
    flex-direction: row;
    height: auto;
    margin-left: 20px;
    margin-right: 20px;
    background-color: #000000 !important;
}
.portfolioExtraCard h5 {
    font-family: 'Cereal' !important;
    font-weight: 900;
    color: #000000 !important;
}
.portfolioExtraCard .chip {
    font-family: 'Cereal' !important;
}

.portfolioExtraDetails {
    background-color: #aeaeb2 !important;
    font-family: 'Cereal' !important;
    color: #b22222 !important;
    display: flex;
    flex-direction: column;
    width: 300px;
}

.portfolioExtraContent {
    font-family: 'Cereal' !important;
    color: #b22222 !important;
    display: flex;
    flex-direction: column;
}

.chip {
    background: rgba(142, 172, 189, 0.2) !important;
    border: 2px solid #38576d !important;
    margin-top: 10px;
}

.portfolioExtraImage {
    height: auto;
    width: 200px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.miniAwardImage {
    background-image: url('./images/award.jpg');
}

.miniPublicationImage {
    background-image: url('./images/publish.jpg');
}

.miniLanguageImage {
    background-image: url('./images/language.png');
}

.miniSkillImage {
    background-image: url('./images/skill.jpg');
}

.portfolioAwardCompany span,
.portfolioAwardDate span {
    font-weight: 600;
}

.portfolioPublishCompany span,
.portfolioPublishDate span,
.portfolioPublishWebsite span {
    font-weight: 600;
}

.portfolioPublishWebsite a {
    color: #152642;
    text-decoration: none;
}

