    font-size: 20px;
    font-weight: 300;
}

.noProfLarge {
    font-size: 30px;
    font-weight: 600;
}

.noProf span {
    font-weight: 700;
    text-transform: uppercase;
    /* font-style: italic; */
    cursor: pointer;
}

.particles-bg {
